@import "../../styles/Variables.sass";

.ms-headshot {
    background-image: url(../../assets/headshot3.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 4px solid $white;
    border-radius: 11px;
    box-shadow: 0 2.8px 6.2px rgba(0, 0, 0, 0.014), 0 6.7px 8.3px rgba(0, 0, 0, 0.014);
    height: 40px;
    width: 40px; }

.ms-horiz-flex-container {
    width: 100%; }

.ms-horiz-flex-container * p {
    font-size: 12px;
    text-align: center; }

.ms-horiz-flex-container * span {
    color: $fontBlue;
    font-size: 14px;
    font-weight: $heavy; }

.ms-horiz-flex-container, .ms-location-container {
    align-items: center;
    display: flex;
    justify-content: space-between; }

.ms-location-icon {
    height: auto;
    margin-right: 3px;
    width: 12px; }

.ms-main {
    margin: 0 30px; }

.ms-menu-button {
    background: $white;
    background-image: url("../../assets/menu-icon.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 20px;
    border-radius: 11px;
    box-shadow: 0 2.8px 6.2px rgba(0, 0, 0, 0.014), 0 6.7px 8.3px rgba(0, 0, 0, 0.014);
    height: 40px;
    width: 40px; }

.ms-search-bar {
    align-items: center;
    background: $white;
    border-radius: 11px;
    box-shadow: 0 2.8px 6.2px rgba(0, 0, 0, 0.014), 0 6.7px 8.3px rgba(0, 0, 0, 0.014);
    display: flex;
    height: 40px;
    margin-top: 40px;
    padding-right: 20px;
    width: 100%; }

.ms-search-bar input {
    color: $mediumGray !important;
    border: none;
    flex-grow: 1; }

.ms-search-icon {
    height: auto;
    margin: auto 20px;
    transform: scaleX(-1);
    width: 16px; }
